import React from 'react';
import { NavLink} from 'react-router-dom';
const Nav = () => {
    return( 
<>
    <div className="container-fluid nav_bg">
        <div className="row">
            <div className="col-10 mx-auto">
                
<nav class="navbar navbar-expand-lg navbar-light bg-light">
  <NavLink class="navbar-brand" to="/">Webcart </NavLink>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="navbarSupportedContent">
    <ul class="navbar-nav ml-auto">
      <li class="nav-item active">
        <NavLink class="nav-link" activeClassName="menu_active" to="/">Home <span class="sr-only">(current)</span></NavLink>
      </li>
      <li class="nav-item">
        <NavLink class="nav-link" activeClassName="menu_active" to="/about">About</NavLink>
      </li>
      <li class="nav-item">
        <NavLink class="nav-link" activeClassName="menu_active" to="/service">Services</NavLink>
      </li>
      <li class="nav-item">
        <NavLink class="nav-link" activeClassName="menu_active" to="/contact">Contact</NavLink>
      </li>
     
    </ul>
 
  </div>
</nav>

</div>
        </div>
    </div>
</>
);
}
export default Nav;