import React from 'react';
import { NavLink } from 'react-router-dom';
const Card = (props) => {
    return( 
    <> 
<div className="col-md-4 col-10 mx-auto">
<div class="card">
  <img class="card-img-top" src={props.imgs} alt="services" />
  <div class="card-body">
    <h5 class="card-title"> {props.name} </h5>
    <p class="card-text"> We also provide trial service to out clients for better experiance .</p>
    <NavLink to="#" class="btn btn-primary"> Buy Service</NavLink>
  </div>
</div>
</div>
</>
);
}
export default Card;