import React from 'react';
import { NavLink } from 'react-router-dom';
import Nav from './Nav';
const Comm = (props) => {
    return (
        <>
            <Nav />
            <section id="header" className="d-flex align-items-center">
                <div className="container-fluid nav_bg">
                    <div className="row">
                        <div className="col-10 mx-auto">
                        <div className="row">        <div className="col-md-6 pt-5 pt-lg-0 order-lg-1 order-2 d-flex justify-content-center flex-column">
<h1> {props.name} <strong className="brand-name"> Webcart </strong></h1>
<h2 className="my-3"> We are team of highly telented web  devlopers </h2>
<div className="mt-3">
    <NavLink to={props.visit} className="btn"> {props.btnname} </NavLink>
</div>
        </div>
        <div className="col-lg-6 order-1 order-lg-2 header-img">
            <img src={props.imgs} alt="home" className="img-fluid animated" />
        </div>
        </div>

                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}
export default Comm;